import * as React from 'react';
import { cva, type VariantProps } from 'class-variance-authority';

import { cn } from '../../lib/utils';

const alertVariants = cva(
  'relative w-full rounded-lg border px-4 py-3 text-sm grid has-[>svg]:grid-cols-[calc(var(--spacing)*4)_1fr] grid-cols-[0_1fr] has-[>svg]:gap-x-3 gap-y-0.5 items-start [&>svg]:size-4 [&>svg]:translate-y-0.5 [&>svg]:text-current',
  {
    variants: {
      variant: {
        default: 'bg-card text-card-foreground',
        destructive:
          'text-destructive bg-card [&>svg]:text-current *:data-[slot=alert-description]:text-destructive/90',
      },
      color: {
        success:
          'text-success bg-success/10 border-success/20 [&>svg]:text-current *:data-[slot=alert-description]:text-success/90',
        warning:
          'text-warning bg-warning/10 border-warning/20 [&>svg]:text-current *:data-[slot=alert-description]:text-warning/90',
        info: 'text-info bg-info/10 border-info/20 [&>svg]:text-current *:data-[slot=alert-description]:text-info/90',
        error:
          'text-error bg-error/10 border-error/20 [&>svg]:text-current *:data-[slot=alert-description]:text-error/90',
      },
    },
    defaultVariants: {
      variant: 'default',
    },
  }
);

function Alert({
  className,
  variant,
  color,
  ...props
}: React.ComponentProps<'div'> &
  VariantProps<typeof alertVariants> & {
    /**
     * The color theme of the alert.
     */
    color?: 'success' | 'warning' | 'info' | 'error';
  }) {
  // If color is specified, use it; otherwise fall back to variant-based color
  const effectiveColor =
    color || (variant === 'destructive' ? 'error' : undefined);

  return (
    <div
      data-slot="alert"
      role="alert"
      className={cn(
        alertVariants({
          variant: color ? 'default' : variant,
          color: effectiveColor,
        }),
        className
      )}
      {...props}
    />
  );
}

function AlertTitle({ className, ...props }: React.ComponentProps<'div'>) {
  return (
    <div
      data-slot="alert-title"
      className={cn(
        'col-start-2 line-clamp-1 min-h-4 font-medium tracking-tight',
        className
      )}
      {...props}
    />
  );
}

function AlertDescription({
  className,
  ...props
}: React.ComponentProps<'div'>) {
  return (
    <div
      data-slot="alert-description"
      className={cn(
        'text-muted-foreground col-start-2 grid justify-items-start gap-1 text-sm [&_p]:leading-relaxed',
        className
      )}
      {...props}
    />
  );
}

export { Alert, AlertTitle, AlertDescription };
